  import { getPointList } from '@/api/user.js'
  import noData from '@/components/noData.vue'
  export default {
    name: 'myIntegral',
    components: {
      noData
    },
    data () {
      return {
        point: '0',
        canget: 0,
        billget: 0,
        rule: null,
        info: [],
        rulestaus: false
      }
    },
    methods: {
      pointList () {
        const postData = {}
        const that = this
        getPointList(postData)
          .then(res => {
            if (res.data.errorcode === 0) {
              that.point = res.data.data.point
              that.rule = res.data.data.rule
              that.info = res.data.data.info
              that.canget = res.data.data.canget
              that.billget = res.data.data.billget
              
            } else {
              this.$vux.toast.text(res.data.errormsg, 'middle')
            }
          })
          .catch(err => {
            this.$vux.toast.text('网络异常！', 'middle')
          })
      },
      ruleClick () {
        this.rulestaus = true
      },
      close () {
        this.rulestaus = false
      }
    },
    created () {
      this.pointList()
}
  }



